"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import api from "@/lib/api";
import Form from "@/components/form";

export default function ClienteForm({ clienteId }) {
  const router = useRouter();
  const isEditing = Boolean(clienteId);

  const [cliente, setCliente] = useState({
    codigo: "",
    nome: "",
    fantasia: "",
    documento: "",
    endereco: "",
  });

  useEffect(() => {
    if (isEditing) {
      api.get(`/clientes/${clienteId}`)
        .then((response) => setCliente(response.data))
        .catch((error) => console.error("Erro ao buscar cliente:", error));
    }
  }, [clienteId, isEditing]);

  const handleChange = (e) => {
    setCliente({ ...cliente, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("token");
      if (!token) {
        alert("Usu√°rio n√£o autenticado");
        return;
      }

      const payload = { ...cliente, usuarioNome: "-" };
      const config = { headers: { Authorization: `Bearer ${token}` } };

      if (isEditing) {
        await api.put(`/clientes/${clienteId}`, payload, config);
      } else {
        await api.post("/clientes", payload, config);
      }

      router.push("/clientes");
    } catch (error) {
      console.error("Erro ao salvar cliente:", error.response?.data || error);
      alert("Erro ao salvar cliente. Veja o console.");
    }
  };

  const fields = [
    { name: "codigo", label: "C√≥digo", required: true, type: "number", disabled: isEditing },
    { name: "nome", label: "Nome", required: true },
    { name: "fantasia", label: "Nome Fantasia" },
    { name: "documento", label: "Documento (CPF/CNPJ)", required: true },
    { name: "endereco", label: "Endere√ßo" },
  ];

  return (
    <Form
      title={isEditing ? "‚úèÔ∏è Editar Cliente" : "üÜï Criar Cliente"}
      fields={fields}
      values={cliente}
      onChange={handleChange}
      onSubmit={handleSubmit}
    />
  );
}
